.success {
    background: green;
    font-size: 20px;
    border-style: solid;
    border-width: 2px;
    padding: 10px;
    margin-bottom: 10px;
}

.error {
    background: red;
    font-size: 20px;
    border-style: solid;
    border-width: 2px;
    padding: 10px;
    margin-bottom: 10px;
}



